.productCard {
  width: 145px;
  height: 220px;
  margin: 10px;
  padding: 15px;
  background-color: #5c53ea;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  -webkit-filter: drop-shadow(0 0 0.75rem rgba(0, 0, 0, 0.445));
          filter: drop-shadow(0 0 0.75rem rgba(0, 0, 0, 0.445));
  border-radius: 15px;
  transition: all 0.3s ease;
  overflow: hidden;
}
.productCard:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}
.productCard .productCard-imgWrapper {
  background-color: #e9e9e9;
  box-shadow: inset 0 0 1em rgba(0, 0, 0, 0.424);
  width: 150%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
}
.productCard .productCard-imgWrapper img {
  width: 60%;
}
.productCard .productCard-info {
  width: 100%;
  height: 45%;
  display: flex;
  padding: 5px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.productCard .productCard-info .info-tittle {
  font-size: 1.5rem;
  color: #01cd02;
  font-family: "Montserrat", sans-serif;
}
.productCard .productCard-info .info-price {
  font-size: 1rem;
  color: #fff;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  letter-spacing: 0.2rem;
}
.productCard .productCard-info .info-bttadd {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 35px;
  padding: 1px;
  color: #fff;
  border-radius: 15px;
  background-color: #d81e02;
  cursor: pointer;
  text-transform: uppercase;
}
.productCard .productCard-info .info-bttadd:hover {
  transition: all 0.3s ease;
  -webkit-filter: drop-shadow(0 0 0.35rem #d81e02);
          filter: drop-shadow(0 0 0.35rem #d81e02);
}
.productCard .productCard-info .info-bttadd .btnWrapper-iconWrapper {
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.productCard .productCard-info .info-bttadd .btnWrapper-iconWrapper img {
  width: 70%;
}
.productCard .productCard-info .info-bttadd h4 {
  font-family: "Montserrat", sans-serif;
  font-size: 0.7rem;
  margin: 0;
